<template>
  <div id="orderCart" class="order-step-1 cart-detail">
    <div class="container">
      <div class="columns" v-if="orderStateNumber<4">
        <div class="column">
          <h1 class="title is-1" translate="yes">Cart</h1>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="columns is-variable is-8 is-centered orderProgressVisual" v-if="orderStateNumber<4">
        <!-- <div class="column is-6"></div> -->
        <div class="column is-8">
          <div class="b-steps">
            <nav class="steps">
              <ul class="step-items">
                <li :class="getActiveStateClasses(1)">
                  <a :class="getClickableStateClasses(1)" @click="setViewCoponentId(1,$event)">
                    <div class="step-marker"><!----></div>
                    <div class="step-details">
                      <span class="step-title">User</span>
                    </div>
                  </a>
                </li>
                <li :class="getActiveStateClasses(2)">
                  <a :class="getClickableStateClasses(2)" @click="setViewCoponentId(2,$event)">
                    <div class="step-marker"><!----></div>
                    <div class="step-details">
                      <span class="step-title">Delivery &amp; Payment</span>
                    </div>
                  </a>
                </li>
                <li :class="getActiveStateClasses(3)">
                  <a :class="getClickableStateClasses(3)" @click="setViewCoponentId(3,$event)">
                    <div class="step-marker"><!----></div>
                    <div class="step-details">
                      <span class="step-title">Summary</span>
                    </div>
                  </a>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="columns is-variable is-8">
        <div class="column is-6" v-if="orderStateNumber<4">
          <div v-if="$store.state.cart!=null && $store.state.cart.items && $store.state.cart.items.length>0">

            <div class="columns is-variable is-8">
              <div class="column is-12">
                <!-- cart table -->
                <div class="b-table">
                  <div class="table-wrapper">
                    <table class="table is-hoverable is-striped is-fullwidth">
                      <thead>
                        <tr>
                          <th class=""><div class="th-wrap" translate="yes">Name</div></th>
                          <th class=""><div class="th-wrap is-pulled-right" translate="yes">Qty</div></th>
                          <th class=""><div class="th-wrap is-pulled-right" translate="yes">Price</div></th>
                          <th class=""></th>
                        </tr>
                      </thead>
                      <tbody>
                        <!-- product -->
                        <tr v-for="(item, i) in $store.state.cart.items" :key="i">
                          <th>
                            <router-link :to="{name: 'productDetail', params: { category: item.categories[0], product: item._id, slug: item.slug }}">
                              <img v-bind:src="getProductImagePath(item.orderCode)" v-bind:alt="item.name[$store.state.language.code]" onerror="this.src='/assets/_site/logo-simple.png'" />
                              {{ item.name[$store.state.language.code] }}
                            </router-link>
                          </th>
                          <td class="cartItemAmount has-text-right">
                            <input class="input" type="number" 
                              v-model="itemAmount[item._id]" 
                              @change="updateCartItemAmount(item._id)"
                              :run="!itemAmount[item._id] ? itemAmount[item._id] = item.amount : true" />
                          </td>
                          <td class="has-text-right">
                            <span>{{ $store.state.numeral(item.price).format('0.00') }}</span>
                            <span>{{ $store.state.currency.symbol }}</span>
                          </td>
                          <td class="has-text-right"><a @click="clearCart(item._id)"><font-awesome-icon icon="trash" /></a></td>
                        </tr>
                        <!-- product END -->
                      </tbody>
                      <tfoot>
                        <tr v-if="$store.state.cart && $store.state.cart.priceTotal">
                          <td></td>
                          <td class="has-text-right"><strong translate="yes">Products Total</strong></td>
                          <td class="has-text-right">
                            {{ $store.state.numeral($store.state.cart.priceTotal).format('0.00') }} 
                            {{ $store.state.currency.symbol }}
                          </td>
                          <td></td>
                        </tr>
                        <tr v-if="$store.state.order && $store.state.order.order && $store.state.order.order.prices && $store.state.order.order.prices.priceDelivery">
                          <td></td>
                          <td class="has-text-right"><span translate="yes">Price delivery</span></td>
                          <td class="has-text-right">
                            {{ $store.state.numeral($store.state.order.order.prices.priceDelivery).format('0.00') }} 
                            {{ $store.state.currency.symbol }}
                          </td>
                          <td></td>
                        </tr>
                        <tr v-if="$store.state.order && $store.state.order.order && $store.state.order.order.prices && $store.state.order.order.prices.pricePayment">
                          <td></td>
                          <td class="has-text-right"><span translate="yes">Price payment</span></td>
                          <td class="has-text-right">
                            {{ $store.state.numeral($store.state.order.order.prices.pricePayment).format('0.00') }} 
                            {{ $store.state.currency.symbol }}
                          </td>
                          <td></td>
                        </tr>
                        <tr v-if="$store.state.order && $store.state.order.order && $store.state.order.order.prices && $store.state.order.order.prices.priceTotal">
                          <td></td>
                          <td class="has-text-right"><strong translate="yes">Total price</strong></td>
                          <td v-if="$store.state.order && $store.state.order.order && $store.state.order.order.prices && $store.state.order.order.prices.pricePayment"
                          class="has-text-right">
                            <strong>{{ $store.state.numeral($store.state.order.order.prices.priceTotal).format('0.00') }}</strong> 
                            {{ $store.state.currency.symbol }}
                          </td>
                          <td v-else-if="$store.state.cart && $store.state.cart.priceTotal!=null"
                          class="has-text-right">
                            <strong>{{ $store.state.numeral($store.state.cart.priceTotal).format('0.00') }}</strong> 
                            {{ $store.state.currency.symbol }}
                          </td>
                          <td></td>
                        </tr>
                      </tfoot>
                    </table>
                  </div>
                </div>
                <!-- cart table end -->
              </div>
            </div>

            <div class="columns is-variable is-8">
              <div class="column is-12">
                <button class="button is-danger" @click="clearCart()">
                  <font-awesome-icon icon="trash" /> &nbsp; <span translate="yes">Clear Cart</span>
                </button>
              </div>
            </div>

          </div>
          <div v-else>
            <h3 class="title is-3"><span translate="yes">Your cart is empty</span></h3>
          </div>
        </div>

        <div class="column is-6">
          <component v-bind:is="orderStateComponent"></component>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import OrderUser from "./User.vue";
import OrderDeliveryPayment from "./DeliveryPayment.vue";
import OrderSummary from "./Summary.vue";
import OrderFinished from "./Finished.vue";

export default {
  name: "OrderCart",
  components: {
    "componentOrderUser": OrderUser,
    "componentOrderDeliveryPayment": OrderDeliveryPayment,
    "componentOrderSummary": OrderSummary,
    "componentOrderFinished": OrderFinished
  },
  data () {
    return {
      'itemAmount': {},
      orderStateNumber: 1, 
      orderParams: {},
      viewComponentId: null
    }
  },
  computed: {
    orderStateComponent: function() {
      let order = this.$store.getters.getOrder;
      this.orderParams = order;
      if ( order && order.result && order.result.id ) {
        this.orderStateNumber = order.result.id;
      }
      let componentId = (this.viewComponentId!=null && this.viewComponentId<=this.orderStateNumber) ?  this.viewComponentId : this.orderStateNumber;
      if (componentId==2) {
        return OrderDeliveryPayment;
      } else if (componentId==3) {
        return OrderSummary;
      } else if (componentId==4) {
        return OrderFinished;
      }
      return OrderUser;
    }
  },
  created: function () {
    if ( !this.orderParams.order || !this.orderParams.order.addresses || !this.orderParams.order.addresses.invoiceAddress ) {
      this.updateOrder();
    }
    console.log(this.orderParams);
  },
  methods: {
    updateOrder() {
      let self = this;
      console.log('orderParams: ', this.orderParams);
      let settings = {
        timeout: 15000
      };  
      if ( typeof this.$store.state.user.token !== 'undefined' && this.$store.state.user.token && this.$store.state.user.token.trim()!='' ) {
        settings["headers"] = {
          "Authorization": "Token "+this.$store.state.user.token
        }
      }
      this.viewComponentId = null;
      this.$httpc.post("order/progress",{
        "orderParams": (typeof this.orderParams.order !== "undefined") ? this.orderParams.order : this.orderParams
      }, settings)
      .then(function (response) {
        // console.log(response, (response.data), (response.data.order), (response.data && response.data.order ) );
        if (response.data && response.data.order ) {
          if ( response.data.order && response.data.order.user && response.data.order.user.token ) {
            self.$store.state.user.logged = true;
            self.$store.state.user.token = response.data.order.user.token;
            self.$store.state.user.token = response.data.order.user.token;
            self.$store.state.cart.user = response.data.order.user.id;
            self.orderStateNumber = response.data.result.id;

            let profile = {
              addressInvoice: response.data.order.addresses.invoiceAddress, 
              addressDelivery: [],
              email: response.data.order.user.email,
              username: response.data.order.user.email
            };
            sessionStorage.setItem("token", response.data.order.user.token);
            self.$httpc.defaults.headers.common['Authorization'] = "Token "+response.data.order.user.token;
            delete response.data.order.user.token;
            self.$store.dispatch("setUser", profile);
            self.$forceUpdate();
          }

          // if order saved and sent
          if ( response.data.order.status=='saved' || response.data.order.status=='sent' ) {
            // cart was cleared
            self.$store.dispatch('setCart',null);
            // order was saved and sent
            // email was sent
            response.data.result.id = 4;
            // get payment url
          }
          self.$store.dispatch('setOrder',response.data);
          // TODO - there are other properties like errors, result, that need to be processed
          if ( response.data.result.id && response.data.result.id>1 ) {
            self.orderStateNumber = response.data.result.id;
          }
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
    },
    updateCartItemAmount(id) {
      let self = this;
      this.$httpc.post("cart", {
        "itemId": id,
        "amount": parseInt(this.itemAmount[id])
      })
      .then( (response) => {
        if (response.data) {
          self.$store.dispatch('setCart',response.data);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        return null;
      })
      .then(function () {
        // always executed
        return null;
      });
    },
    clearCart(itemId) {
      let self = this;
      itemId = (typeof itemId !== 'undefined') ?  itemId : null;
      let deletePromise = null;
      if ( itemId!=null && itemId.toString().trim()!='' ) {
        deletePromise = this.$httpc.delete("cart?itemId="+itemId);
      } else {
        deletePromise = this.$httpc.delete("cart");
      }
      
      deletePromise.then( (response) => {
        console.log(response.data);
        if (response.data) {
          self.$store.dispatch('setCart',response.data);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        return null;
      })
      .then(function () {
        // always executed
        return null;
      });
    },
    getClickableStateClasses(stateNumber) {
      // step-item is-active
      // step-link is-clickable
      if (stateNumber<=this.orderStateNumber) {
        return "step-link is-clickable";
      }
      return "step-link";
    },
    getActiveStateClasses(stateNumber) {
      // step-item is-active
      let classes = "step-item";
      let classPlus = "";
      if ( stateNumber <= this.orderStateNumber ) {
        classPlus = " is-previous";
      }
      if ( (this.viewComponentId && stateNumber==this.viewComponentId) || stateNumber == this.orderStateNumber ) {
        classPlus = " is-active";
      }
      classes += classPlus;
      return classes;
    },
    getStateClass(stateNumber) {
      // console.log(stateNumber+'=='+this.orderStateNumber);
      if (stateNumber==this.orderStateNumber) {
        return "has-text-danger";
      }
      return "";
    },
    setViewCoponentId: function(viewId, event) {
      if (viewId <= this.orderStateNumber) {
        if ( this.$el ) {
          let stepElements = this.$el.querySelectorAll('#orderCart .b-steps .step-item');
          stepElements.forEach(step => {
            step.classList.remove("is-active");
          });
        }
        if ( event && event.target && !event.target.parentElement.parentElement.classList.contains("is-active") ) {
          event.target.parentElement.parentElement.classList.add("is-active");
        }
        this.viewComponentId = viewId;
        this.$forceUpdate();
      }
    },
    getProductImagePath(serialPath) {
      if ( this.$store.state.coredata && this.$store.state.coredata.settings && this.$store.state.coredata.settings.assets && this.$store.state.coredata.settings.assets.url ) {
        return this.$helpers.getProductImagePath(this.$store.state.coredata, serialPath);
      }

      return null;
    }
    
  }
}
</script>

