  .orderProgressVisual {
    margin-bottom: 1em;
  }
  .cartItemAmount {
    input[type=number] {
      width: 60px;
    }
  }
  tbody {
    tr {
      th {
        img {
          height: 32px;
          float: left;
          margin: 0 10px 0 0;
        }
      }
    }
  }
  tfoot {
    tr {
      td {
        border: 0;
        &.has-text-right {
          background: #f2f2f2;
        }
      }
      &:first-child {
        td {
          border-top: 2px solid #dbdbdb;
        }
      }
    }
  }
}
</style>
