<template>
  <div id="meInvoice" class="" v-if="$store.state.user.profile">

    <div class="columns"><div class="column is-8 is-centered">
      <h2 class="title is-2 has-text-centered" translate="yes">Invoice Address</h2>

      <form v-on:submit.prevent="saveUserAddresses">
        <div class="deliveryAddressWrapper">
          <div class="columns is-variable is-2">
            <div class="column is-6">
              <div class="field nameFirst">
                <label class="label" translate="yes">First Name</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.nameFirst" translate="yes" placeholder="First Name">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
            <div class="column is-6">
              <div class="field nameLast">
                <label class="label" translate="yes">Last Name</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.nameLast" translate="yes" placeholder="Last Name">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-12">
              <div class="field street">
                <label class="label" translate="yes">Street</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.street" translate="yes" placeholder="Street">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-12">
              <div class="field street2">
                <label class="label" translate="yes">Street 2</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.street2" translate="yes" placeholder="Street 2">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-4">
              <div class="field zip">
                <label class="label" translate="yes">ZIP</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.zip" translate="yes" placeholder="ZIP">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
            <div class="column is-8">
              <div class="field city">
                <label class="label" translate="yes">City</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.city" translate="yes" placeholder="City">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-6">
              <div class="field state">
                <label class="label" translate="yes">State</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.state" translate="yes" placeholder="State">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
            <div class="column is-6">
              <div class="field country">
                <label class="label" translate="yes">Country</label>
                <div class="control select">
                  <select v-if="$store.state.coredata && $store.state.coredata.countries" v-model="activeInvoiceAddress.country">
                    <option v-for="option in $store.state.coredata.countries" v-bind:value="option.code" v-bind:key="option.code">
                      {{ option.name }}
                    </option>
                  </select>
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2 is-centered">
            <div class="column is-6">
              <div class="field phone">
                <label class="label" translate="yes">Phone</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.phone" translate="yes" placeholder="Phone">
                </div>
                <div class="form-messages is-hidden">
                  <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2 is-centered">
            <div class="column has-text-centered">
              <button class="button is-success">
                <font-awesome-icon icon="check" /> &nbsp; 
                <span translate="yes">Set Invoice Address</span>
              </button>
            </div>
          </div>
        </div>
      </form>

    </div></div>

  </div>
</template>

<script>
export default {
  name: 'UserMeInvoice',
  data () {
    return {
      activeInvoiceAddress: {
        "type":"invoice",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      }
    }
  },
  mounted: function() {
      // get address from store
      let userProfile = this.$store.getters.getUserProfile;
      let invoiceAddressStored;
      if ( userProfile.addressInvoice ) {
        invoiceAddressStored = userProfile.addressInvoice;
      }
      // set default value before any processing
      let result = {
        "type":"invoice",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      };
      // if there is any value from store, use it in result
      if ( invoiceAddressStored && invoiceAddressStored.nameFirst ) {
        result = invoiceAddressStored;
      }
      // if applicaple, use latest values from form
      if ( this.activeInvoiceAddress ) {
        for (var property in this.activeInvoiceAddress) {
          if (this.activeInvoiceAddress.hasOwnProperty(property) && property.trim()!='') {
              this.activeInvoiceAddress[property] = result[property];
          }
        }
      }
      this.$forceUpdate();

      return result;
  },
  methods: {
    saveUserAddresses: function() {
      let addresses = [];
      console.log("userAddressInvoice:", this.activeInvoiceAddress);
      if ( this.$helpers.verifyAddress(this.activeInvoiceAddress) ) {
        addresses.push(this.activeInvoiceAddress);
        addresses.concat(this.$store.state.user.profile.addressesDelivery);
        this.$store.dispatch("updateUser", { "addresses": addresses } );
      }
    }
  }
}
</script>
