<template>
  <div id="meDelivery" class="" v-if="$store.state.user.profile">
    <div class="columns is-variable is-8">
      <div class="column is-8">
        <h2 class="title is-2 has-text-centered" translate="yes">Delivery Addresses</h2>
      </div>
    </div>
    

    <div class="columns">
      
      <div v-if="activeDeliveryAddress" class="column is-8">
        <form v-on:submit.prevent="saveUserAddresses">
          <div class="deliveryAddressWrapper">
            <div class="columns is-variable is-2">
              <div class="column is-6">
                <div class="field nameFirst">
                  <label class="label" translate="yes">First Name</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.nameFirst" translate="yes" placeholder="First Name">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
              <div class="column is-6">
                <div class="field nameLast">
                  <label class="label" translate="yes">Last Name</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.nameLast" translate="yes" placeholder="Last Name">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>

            <div class="columns is-variable is-2">
              <div class="column is-12">
                <div class="field street">
                  <label class="label" translate="yes">Street</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.street" translate="yes" placeholder="Street">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>

            <div class="columns is-variable is-2">
              <div class="column is-12">
                <div class="field street2">
                  <label class="label" translate="yes">Street 2</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.street2" translate="yes" placeholder="Street 2">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>

            <div class="columns is-variable is-2">
              <div class="column is-4">
                <div class="field zip">
                  <label class="label" translate="yes">ZIP</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.zip" translate="yes" placeholder="ZIP">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
              <div class="column is-8">
                <div class="field city">
                  <label class="label" translate="yes">City</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.city" translate="yes" placeholder="City">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>

            <div class="columns is-variable is-2">
              <div class="column is-6">
                <div class="field state">
                  <label class="label" translate="yes">State</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.state" translate="yes" placeholder="State">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
              <div class="column is-6">
                <div class="field country">
                  <label class="label" translate="yes">Country</label>
                  <div class="control select">
                    <select v-if="$store.state.coredata && $store.state.coredata.countries" v-model="activeDeliveryAddress.country">
                      <option v-for="option in $store.state.coredata.countries" v-bind:value="option.code" v-bind:key="option.code">
                        {{ option.name }}
                      </option>
                    </select>
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>

            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6">
                <div class="field phone">
                  <label class="label" translate="yes">Phone</label>
                  <div class="control">
                    <input class="input" type="text" v-model="activeDeliveryAddress.phone" translate="yes" placeholder="Phone">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="is empty" translate="yes">This value cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>

            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6 has-text-centered">
                <button type="submit" class="button is-success">
                  <font-awesome-icon icon="check" /> &nbsp; 
                  <span translate="yes">Address</span>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>

      <div class="column is-4">
        <nav class="panel user-settings-delivery-nav">
          <p class="panel-heading" translate="yes">
            Addresses
          </p>
          <div>
            <div class="panel-block">
              <p class="control has-icons-left">
                <input class="input is-small" type="text" v-model="deliveryFilter" placeholder="search">
                <span class="icon is-small is-left">
                  <i class="fas fa-search" aria-hidden="true"></i>
                </span>
              </p>
            </div>
            <div v-for="(deliveryAddress, d) in userDeliveryAddress" :key="d">
              <div class="panel-block special--right-action">
                <a class="user-delivery--get" @click="getDeliveryAddress(d, $event)">
                  {{deliveryAddress.street}}, {{deliveryAddress.zip}}
                </a>
                <a class="user-delivery--delete" @click="removeDeliveryAddress(d)">
                  <font-awesome-icon icon="trash" />
                </a>
              </div>
            </div>
          </div>
          <div class="panel-block">
            <button class="button is-link is-outlined is-fullwidth" @click="addNewDeliveryAddress">
              <font-awesome-icon icon="plus" /> &nbsp; 
              <span translate="yes">Add New Delivery Address</span>
            </button>
          </div>
        </nav>
      </div>

    </div>


  </div>
</template>

<script>
export default {
  name: 'UserMeDelivery',
  data () {
    return {
      activeDeliveryAddressIndex: 0,
      activeDeliveryAddress: {
        "type":"delivery",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      },
      deliveryFilter: ''
    }
  },
  computed: {
    userDeliveryAddress: function() {
      let userAddressDelivery = this.$store.getters.getUserProfile.addressesDelivery;
      let result = [];

      if ( userAddressDelivery && userAddressDelivery.length>0 ) {
        result = userAddressDelivery;

        // if possible, apply filter
        if ( this.deliveryFilter.toString().trim()!=='' ) {
          let searchPhrase = this.deliveryFilter.toString().trim().toLowerCase();
          let searchResults = [];
          userAddressDelivery.forEach( (element, index) => {
            let foundInElement = false;
            // loop element properties to check for searchPhrase
            for (var property in element) {
              if (element.hasOwnProperty(property)) {
                // check for searchPhrase
                if ( element[property].toString().trim().toLowerCase().indexOf(searchPhrase)>-1 ) {
                  foundInElement = true;
                }
              }
            }
            // if found, add to searchResults
            if ( foundInElement ) {
              searchResults.push(element);
            }
          });
          // if any results, replace original
          if ( searchResults.length>0 ) {
            result = searchResults;
          }
        }
      }

      console.log(result.length);
      return result;
    }
  },
  mounted: function(){
    this.getDeliveryAddress(0);
  },
  methods: {
    getDeliveryAddress: function(indexValue, event) {
      let index = (typeof indexValue !== "undefined" && indexValue>-1) ? indexValue : null;
      if (index===null) {
        index = 0;
      }
      this.activeDeliveryAddressIndex = index;
      this.activeDeliveryAddress = this.userDeliveryAddress[index];

      if (typeof this.activeDeliveryAddress == "undefined" || !this.activeDeliveryAddress) {
        let activeAddress = this.$store.state.user.profile.addressesDelivery[index];
        if ( typeof activeAddress !== "undefined" ) {
          this.activeDeliveryAddress = activeAddress;
        }
      }

      if (event) {
        this.markActiveDeliveryAddress(event.target.parentNode);
      } else {
        this.markActiveDeliveryAddress();
      } 
    },

    addNewDeliveryAddress: function() {
      let self = this;
      // TODO - check if same address does not exist
      this.$store.dispatch("addNewDeliveryAddress", {
        "type":"delivery",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      })
      .then(() => {
        self.getDeliveryAddress();
        self.$forceUpdate();
      });
      this.$forceUpdate();
    },

    removeDeliveryAddress: function(index) {
      let self = this;
      this.$store.dispatch("removeDeliveryAddress", index )
      .then(() => {
        self.activeDeliveryAddressIndex = 0;
        self.$forceUpdate();
        self.saveUserAddresses();
      });
    },

    saveUserAddresses: function() {
      if ( this.$helpers.verifyAddress(this.activeDeliveryAddress) ) {
        let addresses = [];
        let userProfile = this.$store.getters.getUserProfile

        addresses.push(userProfile.addressInvoice);
        if ( userProfile.addressesDelivery.length>0 ) {
          for (let i=0; i<userProfile.addressesDelivery.length; i++) {
            addresses.push(userProfile.addressesDelivery[i]);
          }
        }
        this.$store.dispatch("updateUser", { "addresses": addresses } );
      }
    }, 
    
    markActiveDeliveryAddress: function(elementToActivate) {
      let activeElement = (typeof elementToActivate !== "undefined" && elementToActivate) ? elementToActivate : null;

      this.activeDeliveryAddressIndex = 0;
      if ( this.$store.state.user.profile && this.$store.state.user.profile.addressesDelivery.length>1 ) {
        this.activeDeliveryAddressIndex = this.$store.state.user.profile.addressesDelivery.length-1
      }

      let listItems = this.$el.querySelectorAll('.users-me #meDelivery nav.user-settings-delivery-nav .panel-block.special--right-action');
      if ( listItems && listItems.length ) {
        listItems.forEach(element => {
          element.classList.remove('is-active');
        });
      }

      if ( activeElement ) {
        activeElement.classList.add("is-active");
      } else if ( listItems && listItems.length>0 ) {
        listItems[0].classList.add("is-active");
      }
    }
  }
}
</script>


