<template>
    <article class="tile is-child box">
      <p class="title">{{ product.name[$store.state.language.code] }}</p>
      <p class="subtitle">{{ product.descriptionShort[$store.state.language.code] }}</p>
      <div class="tile-image">
        <img v-bind:src="getProductImagePath(product.orderCode)" v-bind:alt="product.name[$store.state.language.code]" onerror="this.src='/assets/_site/logo-simple.png'" />
      </div>
      <div class="bottom-part">
        <p><span translate="yes">In stock</span> {{ product.stockAmount }}</p>
        <p><span translate="yes">Price</span> {{ $store.state.numeral(product.price).format('0 0.00') }} {{ $store.state.currency.symbol }}</p>
        <div>
          <button class="button is-link" v-bind:data-id="product._id" @click.prevent="addToCart($event)">
            <font-awesome-icon icon="cart-plus" /> &nbsp; <span translate="yes">Add to Cart</span>
          </button>
        </div>
      </div>
    </article>
</template>


<script>
export default {
  props: ['product'],
  methods: {
    addToCart: function(e) {
      let self = this;
      let productId = e.currentTarget.getAttribute('data-id');
      let amount = e.currentTarget.getAttribute('data-amount');

      this.$helpers.animateAddToCart(e.currentTarget);
      if (!amount) {
        amount = 1;
      }
      console.log(productId);
      this.$httpc.put("cart",{
        "itemId": productId,
	      "amount": amount
      })
      .then(function (response) {
        console.log(response);
        if (response.data) {
          self.$store.dispatch('setCart',response.data);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
    },
    getProductImagePath(serialPath) {
      if ( this.$store.state.coredata && this.$store.state.coredata.settings && this.$store.state.coredata.settings.assets && this.$store.state.coredata.settings.assets.url ) {
        return this.$helpers.getProductImagePath(this.$store.state.coredata, serialPath);
      }

      return null;
    }
  }
}
</script>

<style lang="scss">
.tile.is-child.box {
  position: relative;
  .tile-image {
    position: relative;
    img {
      max-height: 200px;
    }
  }
}

.b-flying-img {
  position: absolute;
  width: 200px;
  height: 200px;
  top: 0;
  left: 0;
  z-index: 5;
  transform-origin: top left;
}
</style>

