<template>
  <div id="userOrderList" class="user-order-list">
    <div class="container">
      <div class="columns">
        <div class="column">
          <h1 class="title is-1" translate="yes">Order ID </h1>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="columns is-variable is-8">
        <div class="column is-12">
          <div v-if="orders && orders.length>0">

            <div class="columns is-variable is-8">
              <div class="column is-12">
                <!-- cart table -->
                <div class="b-table">
                  <div class="table-wrapper">
                    <table class="table is-hoverable is-striped is-fullwidth">
                      <thead>
                        <tr>
                          <th class=""><div class="th-wrap" translate="yes">ID</div></th>
                          <th class=""><div class="th-wrap" translate="yes">Date</div></th>
                          <th class=""><div class="th-wrap is-pulled-right" translate="yes">Total</div></th>
                          <th class=""><div class="th-wrap" translate="yes">Status</div></th>
                        </tr>
                      </thead>
                      <tbody>
                        <!-- product -->
                        <tr v-for="(item, i) in orders" :key="i">
                          <th>
                            <router-link :to="{name: 'userOrderDetail', params: { id: item._id }}">
                              {{ item._id }}
                            </router-link>
                          </th>
                          <td class="has-text-right">
                            {{ item.dates.dateCreated }}
                          </td>
                          <td class="cartItemAmount has-text-right">
                            {{ $store.state.numeral(item.prices.priceTotal).format("0.00") }}
                          </td>
                          <td class="has-text-right">
                            {{ item.status }}
                          </td>
                        </tr>
                        <!-- product END -->
                      </tbody>
                    </table>
                  </div>
                </div>
                <!-- cart table end -->
              </div>
            </div>

          </div>
          <div v-else>
            <p translate="yes">You have no finished orders</p>
          </div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: "UserOrderDetail",
  components: {
  },
  data () {
    return {
      filter: {},
      orders: []
    }
  },
  created: function () {
    this.getOrders();
  },
  methods: {
    getOrders() {
      let self = this;
      let settings = {
        timeout: 3000
      }; 
      if ( typeof this.$store.state.user.token !== 'undefined' && this.$store.state.user.token && this.$store.state.user.token.trim()!='' ) {
        settings["headers"] = {
          "Authorization": "Token "+this.$store.state.user.token
        }
      }
      let requestQuery = {};
      this.$httpc.post("order/list", requestQuery, settings)
      .then(function (response) {
        console.log(response, (response.data) );
        if (response && response.data ) {
          self.orders = response.data;
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
    }
    
  }
}
</script>

