<script>
import { mapGetters } from "vuex"

export default {
  name: "OrderUser",
  data () {
    return {
      newUserEmail: "",
      newUserEmail2: "",
      newUserPassword: "",
      newUserPassword2: ""
    }
  },
  computed: {
    ...mapGetters({
      user: "getUserProfile"
    })
  },
  methods: {
    updateUserData: function() {
      console.log( "this.user", this.user );
      // console.log( this.$parent.orderParams );
        if (this.user) {
          // if unregistered user, check email and password data
          let fieldsCheckResult = this.$helpers.propertiesRequiredConfirm({
            "email": this.newUserEmail, 
            "email2": this.newUserEmail, 
            "password": this.newUserPassword, 
            "password2": this.newUserPassword2, 
          }, ["email", "password"], ["email", "password"]);
          console.log("fieldsCheckResult:", fieldsCheckResult);
          if ( fieldsCheckResult.result && this.newUserEmail && this.newUserEmail!="" ) {
            this.user.addressInvoice.email = this.newUserEmail;
            this.$store.state.user.email = this.newUserEmail;
            this.$store.state.password = this.newUserPassword;
            this.$parent.orderParams.order.user.email = this.newUserEmail;
          } else {
            this.$helpers.formUndecorateAndDecorateFields(fieldsCheckResult.fields, fieldsCheckResult.errors);
          }
        }
      let addressInvoiceVerify = Object.assign({}, this.user.addressInvoice);
      delete addressInvoiceVerify.email;
      if ( this.$helpers.verifyAddress(addressInvoiceVerify) ) {
        this.$parent.updateOrder();
        // update user name if different from the one stored
        if ( this.user && this.user!=null && this.user.addressInvoice && this.user.addressInvoice.nameFirst && this.user.addressInvoice.nameLast 
          && this.$store.state.user && this.$store.state.user.profile && this.$store.state.user.profile.addressInvoice.nameFirst 
          && this.$store.state.user.profile.addressInvoice.nameLast ) {
            if ( this.$store.state.user.profile.addressInvoice.nameFirst != this.user.addressInvoice.nameFirst ) {
              this.$store.state.user.profile.addressInvoice.nameFirst = this.user.addressInvoice.nameFirst;
            }
            if ( this.$store.state.user.profile.addressInvoice.nameLast != this.user.addressInvoice.nameLast ) {
              this.$store.state.user.profile.addressInvoice.nameLast = this.user.addressInvoice.nameLast;
            }
        }
        this.$parent.orderParams.order.addresses = {
          invoiceAddress: this.user.addressInvoice
        };
        let userAddressUpdate = {
          addresses: []
        }
        userAddressUpdate.addresses.push(this.user.addressInvoice);
        // self.$store.dispatch("setUser",userAddressUpdate);
      }
    }
  },
  created: function(){
  }
}
</script>

