<template>
  <div v-if="product" class="container product-detail">

    <div class="columns reversed">
      <div class="column is-7">
        <div class="product-image-wrapper">
          <img v-bind:src="getProductImagePath(product.orderCode)" v-bind:alt="product.name[$store.state.language.code]" class="image-main" onerror="this.src='/assets/_site/logo-simple.png'" />
        </div>
      </div>

      <div class="column is-5 data-main">
        <nav class="breadcrumb" aria-label="breadcrumbs" v-if="parentCategoryDetail">
          <ul v-if="parentCategoryDetail.parentCategories">
            <li v-for="(breadcrumb, b) in parentCategoryDetail.parentCategories" :key="b">
              <router-link :title="breadcrumb.name[$store.state.language.code]" :to="{name: 'categoryList', params: { category: breadcrumb.pathSlug}}">
                {{ breadcrumb.name[$store.state.language.code] }}
              </router-link>
            </li>
            <li class="is-active">
              <router-link :title="parentCategoryDetail.name[$store.state.language.code]" :to="{name: 'categoryList', params: { category: parentCategoryDetail.pathSlug}}" aria-current="page">
                {{ parentCategoryDetail.name[$store.state.language.code] }}
              </router-link>
            </li>
          </ul>
        </nav>

        <div v-if="product && product!=null">
          <h1 class="title is-1">{{ product.name.en }}</h1>
          <h2 class="subtitle">{{ product.descriptionShort.en }}</h2>
          <div class="content">
            <p><span translate="yes">Order Code</span>: {{product.orderCode}}</p>
            <button class="button is-link" :data-id="product._id" @click.prevent="addToCart($event)">
              <font-awesome-icon icon="cart-plus" /> &nbsp; <span translate="yes">Add to Cart</span>
            </button>
            <p><span translate="yes">In stock</span> {{ (product && product.stockAmount) ? product.stockAmount : 0 }}</p>
            <p>{{ product.descriptionLong.en }}</p>

            <div v-if="product.data && product.data.variations">
              <section>
                <b-collapse class="card" aria-id="contentIdForA11y3" :open="false">
                    <div slot="trigger" slot-scope="props" class="card-header" role="button" aria-controls="contentIdForA11y3">
                      <p class="card-header-title" translate="yes">
                        Variations
                      </p>
                      <a class="card-header-icon">
                        <b-icon :icon="props.open ? 'angle-up' : 'angle-down'"></b-icon>
                      </a>
                    </div>
                    <div class="card-content">
                      <router-link
                        v-for="(variation, v) in product.data.variations"
                        :key="(variation._id+v)"
                        class="content"
                        :title="variation.name[$store.state.language.code]" 
                        :to="{name: 'productDetail', params: { category: variation.categories[0], product: variation._id, slug: variation.slug }}">
                        <figure class="image is-32x32">
                          <img v-bind:src="getProductImagePath(variation.orderCode)" v-bind:alt="variation.name[$store.state.language.code]" />
                        </figure>
                        {{ variation.name[$store.state.language.code] }}
                      </router-link>
                    </div>
                </b-collapse>
              </section>
            </div>

          </div>
        </div>

      </div>
    </div>


    <div v-if="product && product!=null && product.properties" class="columns product-properties bg-grey">
      <div class="column is-12">
        <h3 class="title is-3 has-text-left" translate="yes">Properties</h3>
        <ul>
          <li v-for="(property, p) in product.properties" :key="p">
            <span>{{ getPropertyStrings(property, p).name }}: &nbsp;</span> 
            <strong>{{ getPropertyStrings(property, p).value }}</strong>
          </li>
        </ul>
      </div>
    </div>

    <div class="columns product-gallery" v-if="product && product!=null && product.data && product.data.gallery && product.data.gallery.images && $store && $store.state && $store.state.coredata">
      <div class="column is-12">
        <h3 class="title is-3 has-text-left" translate="yes">Gallery</h3>
        <slider-carousel :product="product"></slider-carousel>
      </div>
    </div>

    <div class="columns product-related" v-if="product && product!=null && product.data && product.data.related && product.data.related.productResults && product.data.related.productResults.length>0 && $store && $store.state && $store.state.coredata">
      <div class="column is-12">
        <h3 class="title is-3 has-text-left" translate="yes">Related</h3>
        <div class="tile is-ancestor">
          <router-link
            v-for="(product, j) in product.data.related.productResults"
            :key="(product._id+(j))"
            class="tile is-parent is-3"
            :title="product.descriptionShort[$store.state.language.code]" 
            :to="{name: 'productDetail', params: { category: product.activeParentCategoryUrl, product: product._id, slug: product.slug }}">
            <product-box :product="product"></product-box>
          </router-link>
        </div> <!-- tile end -->
      </div>
    </div>

  </div>
</template>

<script>
import SliderCarousel from "./../global/SliderCarousel";
import ProductBox from "./ProductBox.vue";

export default {
  name: "ProductDetail",
  data () {
    return {
      product: null,
      parentCategoryDetail: null,
      language: "en",
      variationModel: null
    }
  },
  components: {
    'slider-carousel': SliderCarousel,
    'product-box': ProductBox
  },
  created: function () {
    // this.language = this.$store.state.language.code;
    this.getProductDetail();
  },
  methods: {
    getProductDetail: function() {
      let self = this;
      if ( this.$route.params && 
          this.$route.params.category && this.$route.params.category.toString().trim()!='' && 
          this.$route.params.product && this.$route.params.product.toString().trim()!='' ) {
        this.$httpc.get("products/"+this.$route.params.category+"/detail/"+this.$route.params.product)
          .then(function (response) {
            // handle success
            if ( response.data ) {
              self.product = response.data;
              if ( self.product.name && self.$store.state && typeof self.product.name[self.$store.state.language.code]!==undefined ) {
                document.title = self.product.name[self.$store.state.language.code] +" - "+ "StretchShop";
              }
              console.log(response.data);
            }
            if ( response.data.products ) {
              self.products = response.data.products;
            }
            if ( response.data.parentCategoryDetail ) {
              self.parentCategoryDetail = response.data.parentCategoryDetail;
            }
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
          });
      }
    },

    addToCart: function(event) {
      let self = this;
      console.log("e", event.currentTarget);
      if ( event.currentTarget ) {
        let productId = event.currentTarget.getAttribute('data-id');
        let amount = event.currentTarget.getAttribute('data-amount');
        let image = this.$el.querySelector('.product-detail img.image-main');

        this.$helpers.animateAddToCart(event.currentTarget, image);
        if (!amount) {
          amount = 1;
        }
        this.$httpc.put("cart",{
          "itemId": productId,
          "amount": amount
        })
        .then(function (response) {
          console.log(response);
          if (response.data) {
            self.$store.dispatch('setCart',response.data);
          }
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
          // always executed
        });
      }
    },

    getProductImagePath(serialPath) {
      if ( this.$store.state.coredata && this.$store.state.coredata.settings && this.$store.state.coredata.settings.assets && this.$store.state.coredata.settings.assets.url ) {
        return this.$helpers.getProductImagePath(this.$store.state.coredata, serialPath);
      }
      return null;
    },

    getProductGalleryImagePath(serialPath, fileName) {
      if ( this.$store.state.coredata && this.$store.state.coredata.settings && this.$store.state.coredata.settings.assets && this.$store.state.coredata.settings.assets.url ) {
        return this.$helpers.getProductImagePath(this.$store.state.coredata, serialPath, fileName);
      }
      return null;
    },

    getPropertyStrings(propertyValue, propertyName) {
      if ( propertyValue.constructor === Array ) { // if array, multiple values without translation
        return { name: propertyName, value: propertyValue.join(", ") };
      } else if ( propertyValue.constructor === Object ) { // if object, it probably contains translation
        if ( typeof propertyValue["name"]!=="undefined" && typeof propertyValue["value"]!=="undefined" && this.$store && this.$store.state && this.$store.state.language ) { // get translates only if .name & .value are set
          let nameResult = "";
          let valueResult = "";
          if ( typeof propertyValue["name"][this.$store.state.language.code]!=="undefined" ) {
            nameResult = propertyValue["name"][this.$store.state.language.code];
          } else {
            nameResult = propertyName["name"];
          }
          console.log( "propertyValue:", propertyValue, typeof propertyValue["value"][this.$store.state.language.code] );
          if ( typeof propertyValue["value"][this.$store.state.language.code]!=="undefined" ) {
            valueResult = propertyValue["value"][this.$store.state.language.code];
          } else {
            valueResult = propertyValue["value"];
          }
          if ( nameResult!=="" && valueResult!=="" ) {
            console.log("INCOMING:", { name: nameResult, value: valueResult } );
            return { name: nameResult, value: valueResult };
          }
        }
        return { name: propertyName, value: propertyValue };
      } else { // simple value without translation
        return { name: propertyName, value: propertyValue };
      }
    }
  }
}
</script>

