/**
 * these are the global helper functions
 */
import AppConstrants from "./constants";

export const helpers = {
  checkIfObjectInArray: (checkObject, checkArray) => {
    if (checkObject && checkObject!=null && checkArray && checkArray instanceof Array && checkArray.length>0) {
      for (let i=0; i<checkArray.length; i++) {
        if ( JSON.stringify(checkArray[i])==JSON.stringify(checkObject) ) {
          return checkObject;
        }
      }
    }
    return null;
  },
  /**
   * Decorate form field with color, icon and message. The recomende format for email form field would be:
   * 
<div class="field email"> // "email" is one name for both - this field and for property that returns in error response JSON
	<div class="control">
		<input class="input" placeholder="Email" type="email" v-model.trim="email" />
	</div>
	<div class="form-messages is-hidden">
		<div class="form-message" data-message="is not found">Email does not exist</div> // "is not found" is error code, that is used in JSON response
	</div>
</div>
   *
   * JSON response would look like this:
{
  "errors": {
    "email": "is not found"
  }
}
   * 
   * 
   * @param {HTMLelement} element - div element with class="field"
   * @param {string} errorMessage - message to display under control
   * @param {string} type - type of form information: danger / success / warning
   */
  decorateFormField: (element, errorMessage, type) => {
    if (errorMessage && errorMessage!=null) {
      // set icon (and just for case even color)
      let typeIconClass = 'exclamation-circle'; // danger
      let typeColorClass = 'isRed';
      switch (type) {
        case "success":
          typeIconClass = 'check';
          typeColorClass = 'isGreen';
          break;
        case "warning":
          typeIconClass = 'exclamation-triangle';
          typeColorClass = 'isYellow';
          break;
      }
      // REMEMBER to remove old decoration before any action using this.$helpers.decorateFormField(element, errorMessage, type);
      let control = element.querySelector('.control');
      if (control && control!=null) {
        control.className += " has-icons-right";
        let fieldControl = control.querySelector('.input');
        if (!fieldControl || fieldControl==null) {
          fieldControl = control;
        }
        fieldControl.className += " is-"+type;
        let icon = document.createElement('span');
        icon.className = 'icon is-right has-text-'+type;
        icon.innerHTML = '<i class="fa fa-'+typeIconClass+'"></i>';
        control.appendChild(icon);
      }
      let text = document.createElement('p');
      text.className = 'help is-'+type;
      text.innerHTML = errorMessage;
      element.appendChild(text);
    }
  },
  /**
   * Removes decoration from element
   * use before calling this.$helpers.decorateFormField(element, message, 'danger') 
   * if you don't want multiple lines of messages
   * 
   * @param {HTMLelement} element 
   * @param {string} element 
   */
  undecorateFormField(element, type) {
    let control = element.querySelector('.control');
    if (control && control!=null) {
      control.classList.remove('has-icons-right');
      let input = control.querySelector('.input');
      if ( input ) {
        input.classList.remove('is-'+type);
      }
      let icon = control.querySelector('span'+'.icon.is-right.has-text-'+type)
      if (icon && icon!=null) {
        console.log(icon);
        control.removeChild(icon)
      }
    }
    let text = element.querySelector('p'+'.help.is-'+type);
    if (text && text!=null) {
      element.removeChild(text);
    }
  },


  /**
   * Simple function to split string into chunks
   * 
   * @param {string} str 
   * @param {int} chunkSize 
   */
  stringChunk(str, chunkSize) {
    chunkSize = (typeof chunkSize === 'undefined') ? 2 : chunkSize;
    let resultString = '';

    if ( str.length>0 ) {
      let resultArray = [];
      let chunk = '';
       for ( let i = 0; i<str.length; i=(i+chunkSize) ) {
         chunk = str.substring(i,i+chunkSize);
         if ( chunk.trim()!='' ) {
           resultArray.push(chunk);
         }
       }
       if (resultArray.length) {
         resultString = resultArray.join('/');
       }
    } else {
      resultString = str;
    }

    return resultString;
  }, 

  /**
   * undecorate and decorate fields
   * 
   * @param {array} undecorateFields - simple array of field class strings to undecorate ["username", "password", ...]
   * @param {array} decorateFields - array of objects that represent name and action to decorate [{"name":"username", "action":"empty"}, ...]
   */
  formUndecorateAndDecorateFields: function(undecorateFields, decorateFields) {
    let self = this;
    undecorateFields.forEach((field) => {
      let element = document.querySelector('.field.'+field);
      if ( element ) {
        console.log('Here I am UNdecorating '+field+":", element);
        self.undecorateFormField(element, 'danger');
      }
    });

    console.log("decorateFields:", decorateFields);
    Object.keys(decorateFields).forEach(function (key) {
      if ( typeof decorateFields[key].name == "undefined" ) {
        decorateFields[key].name = decorateFields[key];
        decorateFields[key].action = "is empty";
      }
      let element = document.querySelector('.field.'+decorateFields[key].name);
      if ( element ) {
        let messageElement = element.querySelector('.form-messages .form-message[data-message="'+decorateFields[key].action+'"]');
        if (messageElement && messageElement!=null) {
          let message = messageElement.innerText;
          console.log('Here I am decorating '+decorateFields[key].name+":", element);
          self.decorateFormField(element, message, 'danger');
        }
      }
    });
  },


  /**
   * 
   * @param {Object} errors - value of error response as represented in error.response.data.errors
   */
  showFormFieldErrorsFromErrorResponse: function(errors) {
    let self = this;
    let undecorate = [];
    let decorate = [];
    Object.keys(errors).forEach(function(key) {
      let name = key;
      let action = errors[key];
      undecorate.push(key);
      decorate.push({"name":name, "action":action});
    });
    if ( undecorate.length>0 ) {
      this.formUndecorateAndDecorateFields(undecorate, decorate);
    }
  },


  /**
   * 
   * @param {object} address 
   */
  verifyAddress: function(address) {
    let requiredFields = [ "nameFirst", "nameLast", "street", "zip", "city", "country", "phone" ];
    let undecorate = [];
    let decorate = [];
    let validAddress = true;
    Object.keys(address).forEach(function(key) {
      undecorate.push(key);
      console.log("address["+key+"]", address[key], requiredFields.indexOf(key));
      if ( address[key].trim()=="" && requiredFields.indexOf(key)>-1 ) {
        let name = key;
        decorate.push({"name":name, "action":"is empty"});
        validAddress = false;
      }
    });
    if ( undecorate.length>0 ) {
      console.log(undecorate, decorate);
      this.formUndecorateAndDecorateFields(undecorate, decorate);
    }
    return validAddress;
  },

  /**
   * 
   * @param {Object} paramObjectToCheck 
   * @param {Array} paramFieldsRequired 
   * @param {Array} paramFieldsConfirm 
   */
  propertiesRequiredConfirm: function(paramObjectToCheck, paramFieldsRequired, paramFieldsConfirm) {
    let objectToCheck = (typeof paramObjectToCheck !== "undefined" && paramObjectToCheck) ? paramObjectToCheck : null;
    let fieldsRequired = (typeof paramFieldsRequired !== "undefined" && paramFieldsRequired) ? paramFieldsRequired : null;
    let fieldsConfirm = (typeof paramFieldsConfirm !== "undefined" && paramFieldsConfirm) ? paramFieldsConfirm : null;

    let fieldsPassed = 0;
    let result = { result: false, fields: [],errors: [] };

    // nothing to check, return false with no errors
    if ( !objectToCheck || objectToCheck==null ) {
      return result;
    }

    fieldsRequired.forEach((field) => {
      result.fields.push(field);
      if ( typeof paramObjectToCheck[field] !== "undefined" && paramObjectToCheck[field]!=null && paramObjectToCheck[field].toString().trim()!="" ) { // check if not empty
        if ( fieldsConfirm.indexOf(field)>-1 ) {
          if ( paramObjectToCheck[field]===paramObjectToCheck[field+"2"] ) {
            fieldsPassed++;
          } else {
            result.errors.push({"name": field+"2", "action": "not confirmed"});
          }
        } else {
          fieldsPassed++;
        }
      } else {
        result.errors.push({"name": field, "action": "is empty"});
      }
    });

    fieldsConfirm.forEach((field) => {
      result.fields.push(field+"2");
    });

    if ( fieldsPassed==fieldsRequired.length ) {
      result.result = true;
    }

    return result;
  },

  /**
   * Use this function on array of objects to get specific parameter by paramater named "code"
   * 
   * @param {Array} values 
   * @param {String} code 
   * @param {String} valuePropertyName 
   */
  getValueByCode(values, code, valuePropertyName) {
    let propertyName = (typeof valuePropertyName !== "undefined") ? valuePropertyName : 'value';
    for( let i=0; i<values.length; i++ ) {
      if (typeof values[i].code !== "undefined" && values[i].code==code) {
        let matchingValue = values[i];
        if (typeof matchingValue[valuePropertyName] !== "undefined") {
          return matchingValue[valuePropertyName];
        }
      }
    }
    return code;
  }, 

  /**
   * 
   * @param {Object} address 
   * @param {Array} countries 
   * @param {Array} ignoreListArray 
   * @param {String} separator 
   */
  addressToString(address, countries, ignoreListArray, separator) {
    let separatorString = (typeof separator !== "undefined") ? separator : "\n";
    let ignoreList = (typeof ignoreListArray !== "undefined") ? ignoreListArray : [];
    let paramForCountry = "country";

    let addressTemplate = "";
    if (typeof AppConstrants.ADDRESS_FORMAT!=="undefined") {
      addressTemplate = AppConstrants.ADDRESS_FORMAT;
    }

    if (address) {
      for (var property in address) {
        if (address.hasOwnProperty(property)) {
          if ( property=="street" ) { // if street param, create full street
            let streetValues = [];
            streetValues.push(address["street"]);
            if ( typeof address["street2"]!=="undefined" && address["street2"].trim()!="" ) {
              streetValues.push(address["street2"]);
            }
            address["addressFull"] = streetValues.join("\n");
            addressTemplate = addressTemplate.replace("{{addressFull}}", address["addressFull"]);
          } else if ( property==paramForCountry ) { // if country, get country name
            addressTemplate = addressTemplate.replace("{{country}}",this.getValueByCode(countries, address[property], "name"));
          } else { // everything else, if not in ignore list, and not empty, just add
            if ( (ignoreList.length>0 && ignoreList.indexOf(property)<0) && address[property].trim()!='' ) {
              addressTemplate = addressTemplate.replace("{{"+property+"}}", address[property]);
            }
          }
        }
      }
    }

    return addressTemplate;
  },

  /**
   * 
   * @param {Object} product 
   */
  getProductUrl(vueInstance, product) {
    let route = { category: null, slug: null, id: null};
    if ( product ) {
      route.category = product.categories[0];
      route.slug = product.slug;
      route.id = product.id;

      let props = vueInstance.$router.resolve({ 
        name: 'productDetail',
        params: route,
      });
      
      return props.href;
    }
    return null;
  }, 

  getProductImagePath(coredata, serialPath, customName) {
    let customFileName = (typeof customName !== "undefined") ? customName : null;
    let url = "https://stretchshop.app/static/";
    let chunk = this.stringChunk(serialPath, 3);

    if ( coredata && coredata.settings && coredata.settings.assets && coredata.settings.assets.url ) {
      url = coredata.settings.assets.url
    }

    if ( customFileName!==null ) {
      return url+"/products/"+chunk+"/"+customFileName;
    }

    return url+"/products/"+chunk+"/"+serialPath+"-default.jpg";
  }, 

  animateAddToCart: function(currentTarget, image) {
    function animate({timing, draw, duration}) {
      let start = performance.now();

      requestAnimationFrame(function animate(time) {
        // timeFraction goes from 0 to 1
        let timeFraction = (time - start) / duration;
        if (timeFraction > 1) timeFraction = 1;

        // calculate the current animation state
        let progress = timing(timeFraction);

        draw(progress); // draw it

        if (timeFraction < 1) {
          requestAnimationFrame(animate);
        }

      });
    }

    let cart = document.querySelector('.userCartLink');
    let cartCountCount = document.querySelector('.userCartLink .cartItemsCount');
    let cartCount = (typeof cartCountCount !== "undefined" && cartCountCount) ? parseInt(cartCountCount.textContent, 10) : 0;

    let cartX = cart.getBoundingClientRect().left + document.body.scrollLeft,
        cartY = cart.getBoundingClientRect().top + document.body.scrollTop;
    cartX = Math.max(cartX, 0);
    cartY = Math.max(cartY, 0);
  
    let el = currentTarget,
        item = el.closest('.tile'); // works only for product list
    let img = null;
    
    if (item) {
      img = item.querySelector('.tile-image img');
    } else if ( (typeof img=="undefined" || !img) && typeof image!=="undefined" ) {
      img = image;
    }
    let imgSrc = img.getAttribute("src");
    let itemToFlyFrom = img;
  
    let flyingToCart = document.createElement("img");
    flyingToCart.className = "b-flying-img";
    flyingToCart.setAttribute("src", imgSrc);
    let imgDims = img.getBoundingClientRect();
    flyingToCart.style.width = imgDims.width;
    flyingToCart.style.height = imgDims.height;
    let imgX = itemToFlyFrom.getBoundingClientRect().left + document.body.scrollLeft;
    imgX = Math.max(imgX, 0);
    let imgY = itemToFlyFrom.getBoundingClientRect().top + document.body.scrollTop;
    imgY = Math.max(imgY, 0);
    let ratioX = ((imgX - cartX) / 100) / 100;
    let ratioY = ((imgY - cartY) / 100) / 100;
    /*
    let ratioX = 0;
    if ( imgX > cartX ) {
      ratioX = ((imgX - cartX) / 100) / 100;
    } else {
      let tempX = Math.abs(cartX);
      ratioX = (((imgX+tempX) - (cartX+tempX)) / 100) / 100;
    }
    let ratioY = 0;
    if ( imgY > cartY ) {
      ratioY = ((imgY - cartY) / 100) / 100;
    } else {
      let tempX = Math.abs(cartX);
      ratioY = ((cartY - imgY) / 100) / 100;
    }
    */
  
    cartCount += 1;
  
    // animation types
    function linear(timeFraction) {
      return timeFraction;
    }
    function circ(timeFraction) {
      return 1 - Math.sin( Math.acos(timeFraction) );
    }
    console.log("imgX, imgY, cartX, cartY, ratioX, ratioY", [imgX, imgY, cartX, cartY, ratioX, ratioY]);
  
    animate({
      duration: 500,
      timing: circ,
      draw: function(progress) {
        flyingToCart.style.top = ((100 * 100 * (1-progress) * ratioY) - imgY + cartY) + 'px'; // update here, not -imgY +cartY if cart is lower
        flyingToCart.style.left = ((100 * 100 * (1-progress) * ratioX) - imgX + cartX) + 'px'; // update here, not -imgY +cartY if cart is on right
        flyingToCart.style.opacity = (1-progress);
        flyingToCart.style.transform = "scale(" + (1-progress) + ")";
        console.log("left, top, opacity, scale", [flyingToCart.style.left, flyingToCart.style.top, flyingToCart.style.opacity, flyingToCart.style.transform]);
        if (progress===1) {
            flyingToCart.remove();
        }
      }
    });
  
    el.parentNode.appendChild(flyingToCart);
  
    cartCountCount.textContent = cartCount;
  }
  
}