<template>
  <div>
    <hooper :settings="hooperSettings" v-if="product && product!=null && product.data && product.data.gallery && product.data.gallery.images && $store && $store.state && $store.state.language">
      <slide v-for="(image, i) in product.data.gallery.images" :key="i" :index="i">
        <a :href="'#'+i" class="image-overlay" @click.prevent="getImageModal($event)">
          <img :src="$parent.getProductGalleryImagePath(product.orderCode, image)" alt="" />
        </a>
      </slide>

      <hooper-navigation slot="hooper-addons"></hooper-navigation>
    </hooper>

    <b-modal v-if="modal.image" :active.sync="modal.isImageModalActive">
        <div class="my-modal-image" :style="{ 'background-image': 'url('+modal.image+')', 'background-size': modal.cssSize }">
        </div>
    </b-modal>
  </div>
</template>

<script>
  import { Hooper, Slide, Navigation as HooperNavigation } from 'hooper';
  import 'hooper/dist/hooper.css';

  export default {
    props: ['product'],
    name: 'SliderCarousel',
    data () {
      return {
        hooperSettings: {
          infiniteScroll: true,
          itemsToShow: this.getItemsToShow()
        },
        modal: {
          isImageModalActive: false,
          image: null, 
          cssSize: "contain"
        }
      }
    },
    components: {
      Hooper,
      Slide,
      HooperNavigation
    },
    methods: {
      getItemsToShow() {
        if ( document.body.clientWidth<=768 ) {
          return 3;
        }
        return 5;
      }, 

      getImageModal(event) {
        if ( event.currentTarget ) {
          let self = this;
          let imageLink = event.currentTarget;
          let image = imageLink.querySelector("img");
          let modal = imageLink.parentElement.parentElement;
          if (image) {
            let imageSrc = image.getAttribute("src");
            if ( imageSrc && imageSrc.trim()!=="" ) {
              let img = new Image();
              img.src = imageSrc;
              img.onload =  function() {
                if ( img.width<window.innerWidth && img.height<window.innerHeight ) {
                  self.modal.cssSize = "auto";
                } else {
                  self.modal.cssSize = "contain";
                }
              }
              this.modal.isImageModalActive = true;
              this.modal.image = imageSrc;
              return;
            }
          }
        }
        this.modal.isImageModalActive = false;
      }

    }
  }
</script>

<style lang="scss">
.hooper {
  .hooper-navigation {
    .hooper-prev, .hooper-next {
      background: rgba(255,255,255,0.5);
      border-color: #888;
      border-style: solid;
      border-width: 1px;
    }
    .hooper-prev {
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      border-left-width: 0px;
    }
    .hooper-next {
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
      border-right-width: 0px;
    }
  }
}

.modal {
  .modal-content, .my-modal-image {
    width: 100%;
    height: 100%;
  }
  .modal-content {
    max-width: 100% !important;
    max-height: 100vh;
  }
  .my-modal-image {
    background-position: center center;
    background-repeat: no-repeat;
  }
}
</style>