<script>
export default {
  name: "UsersRegister",
  data () {
    return {
      sent: false,
      user: {
        "username": "",
        "email": "",
        "email2": "",
        "password": "",
        "password2": "",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "state":"",
        "country":"",
        "phone":""
      }
    }
  },
  created: function () {
    //this.getUsers();
  },
  methods: {
    userRegister: function() {
      let fieldsCheckResult = null;
      let requiredFields = ["username", "email", "password"];//, "nameFirst", "nameLast", "street", "zip", "city", "country", "phone"];
      let fieldsToConfirm = ["email", "password"];
      let self = this;

      // frontend verification
      fieldsCheckResult = this.$helpers.propertiesRequiredConfirm(this.user, requiredFields, fieldsToConfirm);
      if ( fieldsCheckResult.result ) {
        let userToSend = {
          user: {
            username: this.user.username,
            email: this.user.email,
            password: this.user.password
          }
        };
        if ( (this.$store.state.language && this.$store.state.language.code) && (this.$store.state.currency && this.$store.state.currency.code) ) {
          userToSend.user["settings"] = {
            language: this.$store.state.language.code,
            currency: this.$store.state.currency.code
          }
        }
        this.$httpc.post("users/register", userToSend, {
            timeout: 5000
        })
          .then(function (response) {
            // handle success
            console.log(response.data);
            self.sent = true;
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
            self.$helpers.formUndecorateAndDecorateFields(fieldsCheckResult.fields, fieldsCheckResult.errors);
          });
      } else {
        if (fieldsCheckResult && fieldsCheckResult.fields && fieldsCheckResult.errors) {
          this.$helpers.formUndecorateAndDecorateFields(fieldsCheckResult.fields, fieldsCheckResult.errors);
        } else {
          this.$helpers.formUndecorateAndDecorateFields(requiredFields, []);
        }
      }
    }

  }
}
</script>

