<template>
  <div id="summary">
    <h2 class="title is-2" translate="yes">Order summary</h2>
    <form v-on:submit.prevent="submitOrder">
      <div class="columns is-variable is-2">
        <div class="column is-6">
          <h3 class="title is-5" translate="yes">You are ordering to:</h3>
          <div class="summary-block with-pre-whitespace">{{ invoiceAddress() }}</div>
        </div>
        <div class="column is-6" v-if="checkDeliveryAddress()">
          <h3 class="title is-5" translate="yes">With delivery to:</h3>
          <div class="summary-block with-pre-whitespace">{{ deliveryAddress() }}</div>
        </div>
        <div class="column is-6" v-else>
          <h3 class="title is-5" translate="yes">With delivery to:</h3>
          <div class="summary-block" translate="yes">same as invoice address</div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-6">
          <h3 class="title is-5" translate="yes">Your Delivery settings:</h3>
          <div class="content summary-block" v-if="checkDeliveryData()">
            <div v-for="(delivery, d) in deliveryData()" :key="d">
              <p>
                <span class="capitalize">{{ delivery.type }}</span> - 
                <strong>{{ delivery.name }}</strong>: {{ $store.state.numeral(delivery.price).format("0.00") }}&nbsp;{{ $store.state.currency.symbol }}
              </p>
            </div>
          </div>
        </div>
        <div class="column is-6" v-if="checkPaymentData()">
          <div>
            <h3 class="title is-5">Your Payment settings:</h3>
            <div class="content summary-block" v-if="paymentData()">
              <p><strong>{{ paymentData().name }}</strong>: {{ $store.state.numeral(paymentData().price).format("0.00") }}&nbsp;{{ $store.state.currency.symbol }}</p>
            </div>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-12">
          <div class="field has-text-centered">
            <h3 class="title is-5">
              <span translate="yes">Total price</span>: 
              <span>
                {{ $store.state.numeral($store.state.order.order.prices.priceTotal).format("0.00") }}&nbsp;{{ $store.state.currency.symbol }}
              </span>
            </h3>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-12">
          <div class="field confirm has-text-centered">
            <label class="b-checkbox checkbox">
              <input type="checkbox" @click="setConfirmationDate($event)">
              <span class="check is-success"></span>
              <span class="control-label" translate="yes">I agree to the</span>&nbsp;<a href="#" translate="yes">terms and conditions</a>
            </label>
            <div class="form-messages is-hidden">
              <div class="form-message" data-message="unconfirmed" translate="yes">Check this to enable sending order</div>
            </div>
          </div>
        </div>
      </div>

      <button class="button is-success">
        <font-awesome-icon icon="check" /> &nbsp; 
        <span translate="yes">Submit order</span>
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: "OrderDeliveryPayment",
  data () {
    return {
			userConfirmation: null
    }
  },
  computed: {
  },
  methods: {
    submitOrder: function() {
      // remove any decoration
      let element = this.$el.querySelector('#summary .field.confirm .check');
      element.classList.remove('is-danger');
      if ( element ) {
        element.parentElement.querySelector('.control-label').classList.add('has-text-danger');
      }

      if ( !this.userConfirmation ) {
        // add decoration for missing confirmation
        this.$helpers.formUndecorateAndDecorateFields(["confirm"], [{name:"confirm", action:"unconfirmed"}]);
        element.classList.remove('is-success');
        element.classList.remove('is-dang');
        element.classList.add('is-danger');
        if ( element ) {
          element.parentElement.querySelector('.control-label').classList.add('has-text-danger');
        }
      } else {
        this.$parent.orderParams.order.dates.userConfirmation = this.userConfirmation;
        this.$parent.updateOrder();
      }
    },
    setConfirmationDate: function(event) {
      if (event.target.checked) {
        let nowTime = new Date();
        this.userConfirmation = nowTime.getTime(); //on checkbox check click;
      }
    },
    checkDeliveryAddress: function() {
      if ( this.$parent.orderParams && this.$parent.orderParams.order && this.$parent.orderParams.order.addresses && this.$parent.orderParams.order.addresses.deliveryAddress && Object.keys(this.$parent.orderParams.order.addresses.deliveryAddress).length>0 ) {
        return true;
      }
      return false;
    },
    invoiceAddress: function() {
      if ( this.$store.state.coredata ) {
        return this.$helpers.addressToString(this.$parent.orderParams.order.addresses.invoiceAddress, this.$store.state.coredata.countries, ["type"]);
      }
    },
    deliveryAddress: function() {
      if (this.$parent.orderParams.order.addresses.deliveryAddress) {
        return this.$helpers.addressToString(this.$parent.orderParams.order.addresses.deliveryAddress, this.$store.state.coredata.countries, ["type"]);
      } else {
        return null;
      }
    },
    checkDeliveryData: function() {
      if ( this.$parent.orderParams.order && this.$parent.orderParams.order.data.deliveryData && this.$parent.orderParams.order.data.deliveryData.codename ) {
        return true;
      }
      return false;
    },
    deliveryData: function() {
      let self = this;
      let deliveriesToDisplay = [];
      // check if delivery data object exists
      if ( this.$parent.orderParams.order && this.$parent.orderParams.order.data.deliveryData && this.$parent.orderParams.order.data.deliveryData.codename ) {
        let deliveryCodenames = this.$parent.orderParams.order.data.deliveryData.codename;
        let deliveryTypesTranslates = this.$parent.orderParams.order.settings.deliveryMethodsTypes;
        // loop delivery type codenames (physical, digital)
        Object.keys(deliveryCodenames).forEach(item => {
          // if item exists, create delivery method object
          if ( deliveryCodenames && deliveryTypesTranslates && deliveryCodenames[item] && deliveryCodenames[item]!=null ) {
            let deliveryMethod = deliveryCodenames[item];
            deliveryMethod["type"] = item;
            deliveryMethod["name"] = deliveryMethod.value;
            // loop delivery type translates and set name when match found
            if ( deliveryTypesTranslates[item] ) {
              deliveryTypesTranslates[item].forEach(type => {
                if (type.type && type.codename && type.type==item && type.codename==deliveryMethod["value"]) {
                  deliveryMethod["name"] = type.name[self.$store.getters.getLanguage];
                }
              });
            }
            // add delivery method to array only if translation exists
            deliveriesToDisplay.push(deliveryMethod);
          }
        });
        return deliveriesToDisplay;
      }
    },
    checkPaymentData: function() {
      if ( this.$parent.orderParams && this.$parent.orderParams.order && this.$parent.orderParams.order.data.paymentData ) {
        return true;
      }
      return false;
    },
    paymentData: function() {
      let self = this;
      let paymentToDisplay = null; //{ name: "", price: 0 };
      // check if payment data object exists
      if ( this.$parent.orderParams && this.$parent.orderParams.order && this.$parent.orderParams.order.data.paymentData ) {
        let name = typeof this.$parent.orderParams.order.data.paymentData.name[self.$store.getters.getLanguage] !== "undefined" ? this.$parent.orderParams.order.data.paymentData.name[self.$store.getters.getLanguage] : this.$parent.orderParams.order.data.paymentData.codename;
        paymentToDisplay = {
          name: name,
          price: this.$parent.orderParams.order.data.paymentData.price
        };
      }
      console.log("paymentToDisplay", paymentToDisplay);
      return paymentToDisplay;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
