.capitalize {
  text-transform: capitalize;
}
.summary-block {
  text-align: left;
  border-radius: 5px;
  background: #f2f2f2;
  padding: 10px 15px;
  &.with-pre-whitespace {
    white-space: pre;
  }
}
</style>
