<template>
  <div id="userOrderDetail" v-if="order">

    <div class="container">
      <div class="columns is-variable is-8 is-centered">
        <div class="column is-8">
          <nav class="breadcrumb" aria-label="breadcrumbs">
            <ul>
              <li>
                <router-link :to="{name: 'userOrders'}">
                  <span translate="yes">Orders</span>
                </router-link>
              </li>
              <li class="is-active">
                <a aria-current="page">
                  <span translate="yes">Order</span> <span class="order-title">#<strong>{{ splitTitle(order._id)[0] }}</strong>{{ splitTitle(order._id)[1] }}</span>
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>

    <h2 class="title is-2">
      <span translate="yes">Order</span> 
      <span class="order-title">
        #<strong>{{ splitTitle(order._id)[0] }}</strong>{{ splitTitle(order._id)[1] }}
      </span>
    </h2>

    <div class="container">
      <div class="columns is-variable is-8 is-centered orderProgressVisual" v-if="order.dates">
        <!-- <div class="column is-6"></div> -->
        <div class="column is-8">
          <div class="b-steps">
            <nav class="steps">
              <ul class="step-items">
                <li :class="order.dates.dateCreated ? 'step-item is-previous' : 'step-item'">
                  <a class="step-link">
                    <div class="step-marker"><!----></div>
                    <div class="step-details">
                      <span class="step-title">
                        <span translate="yes">Created</span>
                        <span class="step-date">{{ order.dates.dateCreated }}</span>
                      </span>
                    </div>
                  </a>
                </li>
                <li :class="order.dates.datePaid ? 'step-item is-previous' : 'step-item'">
                  <a class="step-link">
                    <div class="step-marker"><!----></div>
                    <div class="step-details">
                      <span class="step-title">
                        <span>Paid</span>
                        <span class="step-date">{{ order.dates.datePaid }}</span>
                      </span>
                    </div>
                  </a>
                </li>
                <li :class="order.dates.dateExpeded ? 'step-item is-previous' : 'step-item'">
                  <a class="step-link">
                    <div class="step-marker"><!----></div>
                    <div class="step-details">
                      <span class="step-title">
                        <span translate="yes">Expeded</span>
                        <span class="step-date">{{ order.dates.dateExpeded }}</span>
                      </span>
                    </div>
                  </a>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>

    <div class="container order-detail-data">
      <div class="columns is-variable is-8">
        <div class="column is-12">
          <span>Your available actions:</span>
            <div v-if="this.order.data.paymentData.codename=='online_braintree'">
              <div id="payment-braintree-dropin-container"></div>
              <button id="payment-braintree-payment-button" class="button is-primary" translate="yes">Pay The order</button> 
            </div>
            <div v-else-if="this.order.data.paymentData.codename=='online_paypal_paypal'">
              <button v-if="order.dates && !order.dates.datePaid" type="button" class="button" @click="makePayment()"><span>Pay order</span></button>
            </div>
        </div>
      </div>
    </div>

    <div class="container order-detail-data">
      <div class="columns is-variable is-8">
        <div class="column is-6">
          <div v-if="order.items!=null && order.items && order.items.length>0">

            <div class="columns is-variable is-8">
              <div class="column is-12">
                <!-- cart table -->
                <div class="b-table">
                  <div class="table-wrapper">
                    <table class="table is-hoverable is-striped is-fullwidth">
                      <thead>
                        <tr>
                          <th class=""><div class="th-wrap" translate="yes">Name</div></th>
                          <th class=""><div class="th-wrap is-pulled-right" translate="yes">Qty</div></th>
                          <th class=""><div class="th-wrap is-pulled-right" translate="yes">Price</div></th>
                        </tr>
                      </thead>
                      <tbody>
                        <!-- product -->
                        <tr v-for="(item, i) in order.items" :key="i">
                          <th>
                            {{ item.name[$store.state.language.code] }}
                          </th>
                          <td class="cartItemAmount has-text-right">
                            {{ item.amount }}
                          </td>
                          <td class="has-text-right">
                            <span>{{ $store.state.numeral(item.price).format('0.00') }}</span>
                            <span>{{ $store.state.currency.symbol }}</span>
                          </td>
                        </tr>
                        <!-- product END -->
                      </tbody>
                      <tfoot>
                        <tr v-if="order.prices && order.prices.priceItems">
                          <td></td>
                          <td class="has-text-right"><strong translate="yes">Products Total</strong></td>
                          <td class="has-text-right">
                            {{ $store.state.numeral(order.prices.priceItems).format('0.00') }} 
                            {{ $store.state.currency.symbol }}
                          </td>
                        </tr>
                        <tr v-if="order.prices && order.prices.priceDelivery">
                          <td></td>
                          <td class="has-text-right"><span translate="yes">Price delivery</span></td>
                          <td class="has-text-right">
                            {{ $store.state.numeral(order.prices.priceDelivery).format('0.00') }} 
                            {{ $store.state.currency.symbol }}
                          </td>
                        </tr>
                        <tr v-if="order.prices && order.prices.pricePayment">
                          <td></td>
                          <td class="has-text-right"><span translate="yes">Price payment</span></td>
                          <td class="has-text-right">
                            {{ $store.state.numeral(order.prices.pricePayment).format('0.00') }} 
                            {{ $store.state.currency.symbol }}
                          </td>
                        </tr>
                        <tr v-if="order.prices && order.prices.priceTotal">
                          <td></td>
                          <td class="has-text-right"><strong translate="yes">Total price</strong></td>
                          <td class="has-text-right">
                            <strong>{{ $store.state.numeral(order.prices.priceTotal).format('0.00') }}</strong> 
                            {{ $store.state.currency.symbol }}
                          </td>
                        </tr>
                      </tfoot>
                    </table>
                  </div>
                </div>
                <!-- cart table end -->
              </div>
            </div>

          </div>
          <div v-else>
            <h3 class="title is-3"><span translate="yes">No items in this order</span></h3>
          </div>
        </div>
        <div class="column is-6">
          <div class="columns is-variable is-2">
            <div class="column is-6">
              <h3 class="title is-5" translate="yes">You are ordering to:</h3>
              <div class="summary-block with-pre-whitespace">{{ invoiceAddress() }}</div>
            </div>
            <div class="column is-6" v-if="checkDeliveryAddress()">
              <h3 class="title is-5" translate="yes">With delivery to:</h3>
              <div class="summary-block with-pre-whitespace">{{ deliveryAddress() }}</div>
            </div>
            <div class="column is-6" v-else>
              <h3 class="title is-5" translate="yes">With delivery to:</h3>
              <div class="summary-block" translate="yes">same as invoice address</div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-6">
              <h3 class="title is-5" translate="yes">Your Delivery settings:</h3>
              <div class="content summary-block" v-if="checkDeliveryData()">
                <div v-for="(delivery, d) in deliveryData()" :key="d">
                  <p>
                    <span class="capitalize">{{ delivery.type }}</span> - 
                    <strong>{{ delivery.name }}</strong>: {{ $store.state.numeral(delivery.price).format("0.00") }}&nbsp;{{ $store.state.currency.symbol }}
                  </p>
                </div>
              </div>
            </div>
            <div class="column is-6" v-if="checkPaymentData()">
              <div>
                <h3 class="title is-5">Your Payment settings:</h3>
                <div class="content summary-block" v-if="paymentData()">
                  <p><strong>{{ paymentData().name }}</strong>: {{ $store.state.numeral(paymentData().price).format("0.00") }}&nbsp;{{ $store.state.currency.symbol }}</p>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "userOrderDetail",
  data () {
    return {
			order: null
    }
  },
  created: function () {
    this.getOrder(this.$route.params.id);
  },
  methods: {
    getOrder(id) {
      let self = this;
      let settings = {
        timeout: 3000
      }; 
      if ( typeof this.$store.state.user.token !== 'undefined' && this.$store.state.user.token && this.$store.state.user.token.trim()!='' ) {
        settings["headers"] = {
          "Authorization": "Token "+this.$store.state.user.token
        }
      }
      let requestQuery = {"query": {"_id": id}, "limit": 1};
      this.$httpc.post("order/list", requestQuery, settings)
      .then(function (response) {
        if (response && response.data && response.data.length==1 ) {
          self.order = response.data[0];
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
    },
    splitTitle(title) {
      return [title.slice(0,7), title.slice(7)];
    },
    checkDeliveryAddress: function() {
      if ( this.order && this.order.addresses && this.order.addresses.deliveryAddress && Object.keys(this.order.addresses.deliveryAddress).length>0 ) {
        return true;
      }
      return false;
    },
    invoiceAddress: function() {
      if ( this.$store.state.coredata ) {
        return this.$helpers.addressToString(this.order.addresses.invoiceAddress, this.$store.state.coredata.countries, ["type"]);
      }
    },
    deliveryAddress: function() {
      if (this.order.addresses.deliveryAddress) {
        return this.$helpers.addressToString(this.order.addresses.deliveryAddress, this.$store.state.coredata.countries, ["type"]);
      } else {
        return null;
      }
    },
    checkDeliveryData: function() {
      if ( this.order && this.order.data.deliveryData && this.order.data.deliveryData.codename ) {
        return true;
      }
      return false;
    },
    deliveryData: function() {
      let self = this;
      let deliveriesToDisplay = [];
      // check if delivery data object exists
      if ( this.order && this.order.data.deliveryData && this.order.data.deliveryData.codename ) {
        let deliveryCodenames = this.order.data.deliveryData.codename;
        console.log( "deliveryCodenames: ", deliveryCodenames, this.order );
        if ( this.order.settings && this.order.settings.deliveryMethods ) {
          let deliveryTypesTranslates = this.order.settings.deliveryMethods;
          // loop delivery type codenames (physical, digital)
          Object.keys(deliveryCodenames).forEach(item => {
            // if item exists, create delivery method object
            if ( deliveryCodenames && deliveryTypesTranslates && deliveryCodenames[item] && deliveryCodenames[item]!=null ) {
              let deliveryMethod = deliveryCodenames[item];
              deliveryMethod["type"] = item;
              deliveryMethod["name"] = deliveryMethod.value;
              // loop delivery type translates and set name when match found
              if ( deliveryTypesTranslates[item] ) {
                deliveryTypesTranslates[item].forEach(type => {
                  if (type.type && type.codename && type.type==item && type.codename==deliveryMethod["value"]) {
                    deliveryMethod["name"] = type.name[self.$store.getters.getLanguage];
                  }
                });
              }
              // add delivery method to array only if translation exists
              deliveriesToDisplay.push(deliveryMethod);
            }
          });
        }
        return deliveriesToDisplay;
      }
    },
    checkPaymentData: function() {
      if ( this.order && this.order.data.paymentData ) {
        return true;
      }
      return false;
    },
    paymentData: function() {
      let self = this;
      let paymentToDisplay = null; //{ name: "", price: 0 };
      // check if payment data object exists
      if ( this.order && this.order.data.paymentData ) {
        let name = typeof this.order.data.paymentData.name[self.$store.getters.getLanguage] !== "undefined" ? this.order.data.paymentData.name[self.$store.getters.getLanguage] : this.order.data.paymentData.codename;
        paymentToDisplay = {
          name: name,
          price: this.order.data.paymentData.price
        };
      }
      return paymentToDisplay;
    },

    makePayment() {
      console.log("this.order.data.paymentData.codename", this.order.data.paymentData.codename);
      if ( this.order.data.paymentData.codename=="online_braintree" ) {
        let braintreeScript = document.createElement('script');
        let self = this;
        braintreeScript.onload = function () {
            //do stuff with the script
            self.paymentBraintreeLoaded();
        };
        braintreeScript.setAttribute('src', 'https://js.braintreegateway.com/web/dropin/1.16.0/js/dropin.min.js');
        braintreeScript.async = true;
        document.head.appendChild(braintreeScript);
      } else if ( this.order.data.paymentData.codename.indexOf("online_paypal")>-1 ) {
        this.paymentPayPalCall();
      }
    },
    paymentBraintreeLoaded: function() {
      let paymentButton = this.$el.querySelector("#payment-braintree-payment-button");
      let self = this;

      this.$httpc.get("/users/btut", {
        timeout: 5000
      })
      .then( (response) => {
        if (response.data && response.data.result && response.data.result==="success" && response.data.token) {
          //console.log("response.data: ", response.data);
          braintree.dropin.create({
            authorization: response.data.token,
            container: '#payment-braintree-dropin-container',
            paypal: {
              flow: 'checkout'
            }
          }, function (createErr, instance) {
            console.log(createErr, instance);
            paymentButton.addEventListener('click', function () {
              instance.requestPaymentMethod(function (err, payload) {
                // Submit payload.nonce to your server
                console.log('----- braintree payload -----');
                console.log(payload);
                self.$httpc.post("/order/btcheckout", {
                  "orderId": self.order._id,
                  "checkoutData": payload
                }, { timeout: 5000 })
                .then( (response) => {
                  console.log('---- braintree transaction response ----');
                  console.log(payload);
                });
              });
            });
          });  
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        return null;
      })
      .then(function () {
        // always executed
        return null;
      });
    }, 
    paymentPayPalCall: function() {
      let self = this;
      self.$httpc.post("/order/paypalcheckout", {
        "orderId": self.order._id,
        "checkoutData": null
      }, { timeout: 5000 })
      .then( (response) => {
        if (response && response.data && response.data.url && response.data.url.trim()!="") {
          window.location.href = response.data.url;
        }
      });
    }


  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
