#userOrderDetail {
  .order-title {
    display: block;
    font-weight: normal;
    strong {
      font-weight: bold;
    }
  }
  .b-steps {
    margin: 10px 0 20px 0;
    .steps {
      .step-items {
        .step-title {
          .step-date {
            display: block;
            font-size: 0.6rem;
          }
        }
      }
    }
  }
  .order-detail-data {
    margin-bottom: 20px;
  }
}
</style>
